






































	export default {
		data() {
			return {
				phone: "",
				positionTop: 0,
				focusIndex: 0,
				focus: true
			};
		},
		methods: {
			initPosition() {
				this.positionTop = uni.getSystemInfoSync().windowHeight - 140;
			},
			goPassword() {
				uni.navigateTo({
					url: '../password/password?phone=' + this.phone
				});
			},
			goWechatLogin() {
				uni.navigateTo({
					url: '../../login-wechat/login-wechat'
				})
			},
			iptFocus() {
				this.focusIndex = 0
// 				console.log(this.focus)
// 				this.focus = true
			},
// 			captchaIpt() {
// 				this.focusIndex = 0
// 			},
			nextFocus(e) {
				
				if(e.detail.value.length == 1 && this.focusIndex < 3) {
					this.focusIndex += 1
					console.log(this.focusIndex)
				} else if(e.detail.value.length == 0 && this.focusIndex > 0) {
					this.focusIndex -= 1
					console.log(this.focusIndex)
				}
			},
			firstFocus(e) {
				this.focusIndex = 0
				console.log(this.focusIndex)
			}
		},
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
			this.phone = option.phone;
			this.initPosition();
			this.focusIndex = 0;
		}
	}



